'use client';

import { useState } from "react";
import Image from "next/image";

export default function AuthPage() {
  const [isRegisterActive, setIsRegisterActive] = useState(false);

  return (
    <div className="flex min-h-screen items-center justify-center bg-gray-100 px-4 py-12">
      <div className="relative w-full max-w-4xl shadow-xl rounded-2xl overflow-hidden flex md:flex-row flex-col">

        {/* Login Form */}
        <div className={`w-full md:w-1/2 bg-white p-8 transition-transform duration-700 ease-in-out ${isRegisterActive ? '-translate-x-full md:translate-x-0' : 'translate-x-0'}`}>
          <div className="flex justify-center mb-6">
            <Image src="/Logo.png" alt="Logo" width={100} height={100} />
          </div>
          <h2 className="text-center text-2xl font-bold text-gray-900 mb-6">Sign In</h2>
          <form className="space-y-4">
            <input type="email" placeholder="Email" className="w-full px-3 py-2 rounded-md bg-gray-100 text-gray-900 focus:outline-none focus:ring-2 focus:ring-indigo-500" />
            <input type="password" placeholder="Password" className="w-full px-3 py-2 rounded-md bg-gray-100 text-gray-900 focus:outline-none focus:ring-2 focus:ring-indigo-500" />
            <button className="w-full py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-500 transition">Sign In</button>
          </form>
        </div>

        {/* Register Form */}
        <div className={`absolute top-0 right-0 w-full md:w-1/2 h-full bg-white p-8 transition-transform duration-700 ease-in-out ${isRegisterActive ? 'translate-x-0 md:translate-x-0' : 'translate-x-full md:translate-x-0'}`}>
          <div className="flex justify-center mb-6">
            <Image src="/Logo.png" alt="Logo" width={100} height={100} />
          </div>
          <h2 className="text-center text-2xl font-bold text-gray-900 mb-6">Register</h2>
          <form className="space-y-4">
            <input type="text" placeholder="Full Name" className="w-full px-3 py-2 rounded-md bg-gray-100 text-gray-900 focus:outline-none focus:ring-2 focus:ring-indigo-500" />
            <input type="email" placeholder="Email" className="w-full px-3 py-2 rounded-md bg-gray-100 text-gray-900 focus:outline-none focus:ring-2 focus:ring-indigo-500" />
            <input type="password" placeholder="Password" className="w-full px-3 py-2 rounded-md bg-gray-100 text-gray-900 focus:outline-none focus:ring-2 focus:ring-indigo-500" />
            <input type="password" placeholder="Confirm Password" className="w-full px-3 py-2 rounded-md bg-gray-100 text-gray-900 focus:outline-none focus:ring-2 focus:ring-indigo-500" />
            <button className="w-full py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-500 transition">Sign Up</button>
          </form>
        </div>

        {/* Dark Overlay Panel */}
        <div
          onClick={() => setIsRegisterActive(!isRegisterActive)}
          className={`absolute top-0 left-0 md:left-1/2 w-full md:w-1/2 h-full bg-blue-900/95 text-white flex flex-col items-center justify-center cursor-pointer transition-transform duration-700 ease-in-out ${isRegisterActive ? '-translate-x-full md:-translate-x-full' : 'translate-x-0 md:translate-x-0'}`}
        >
          <span className="text-2xl font-bold">
            {isRegisterActive ? 'Sign In' : 'Register'}
          </span>
          <p className="mt-2 text-center max-w-xs text-white/80">
            {isRegisterActive
              ? 'Click to go back to login'
              : 'Click to create a new account'}
          </p>
        </div>
      </div>
    </div>
  );
}
